@mixin bord-l-r {
	.h-line {
		border-left: 1px solid #3c3c3c;
		border-right: 1px solid #3c3c3c; } }

@mixin site-h {
	.site-h {
		text-transform: uppercase;
		font-size: 40px;
		font-family: "FuturaPTB";
		margin-top: 110px;
		margin-bottom: 70px;
		color: $text;
		text-align: center;
		span {
			color: $green; } } }

// @mixin placeholder ($css)
// 	::-webkit-input-placeholder $css
// 	:-moz-placeholder           $css
// 	::-moz-placeholder          $css
// 	:-ms-input-placeholder      $css

@mixin placeholder() {
  input::-webkit-input-placeholder {
    @content; }
  input:-moz-placeholder {
    @content; }
  input::-moz-placeholder {
    @content; }
  input:-ms-input-placeholder {
    @content; } }

@mixin transition($time) {
	transition: $time ease-out;
	-webkit-transition:  $time ease-out;
	-moz-transition:  $time ease-out;
	-ms-transition:  $time ease-out;
	-o-transition:  $time ease-out; }

@mixin transition-ease($time) {
	transition: $time ease;
	-webkit-transition:  $time ease;
	-moz-transition:  $time ease;
	-ms-transition:  $time ease;
	-o-transition:  $time ease; }


