@import "mixin";

@media all and (max-width: 720px) {

	body {
		min-width: $w375;
		margin: auto; }
	.bunner {
		background: $white;
		max-width: $w375;
		margin: auto;
		position: relative;
		.ninja {
				background: url("../image/Menu-1.png") center center no-repeat; }
		nav {
			height: 1259px;
			a {
				color: $white; }
			.logo {
				div {
					color: $white; } } }
		.st-effect-1.st-menu-open .st-effect-1.st-menu {
			visibility: visible;
			transform: translate3d(340%, 0, 0);
			z-index: 300; } }

	.picture {
		width: $w375;
		margin: auto;
		background: url("../image/Picture-9.png") center center no-repeat;
		background-size: cover;
		padding: 20px 0;
		.caption {
			font-size: 28px;
			margin: 15px auto; } }


	.text {
		width: $w375;
		margin-top: 30px;
		.column {
			width: 90%;
			margin: auto;
			display: block; }
		.column:first-child {
			p:nth-child(3):after {
				content: 'Ask yourself: what will the world look like in thirty years? Electric cars with autopilot? Doctors that can diagnose you over the Internet? Self - cleaning plates and self -recycling trash? Whatever you have in mind the key to achieving it is the correct technology.'; }
			p:nth-child(5):after {
				content: '\00AD'; }
			p:nth-child(5):after {
				content: '\00AD In the age of technology, often people with the greatest technological ideas have absoluted.'; } }
		.column:last-child {
			display: none; } }

	.button {
		margin: 30px auto 40px; } }

